import "./About.css";
import useTimer from "./aboutHooks/useTimer";
import Smiley from "../../assets/About/Smiley.jsx";

export default function About() {
  // Custom Hook
  const currentTime = useTimer();

  return (
    <>
      <div id="about" className="about-wrapper">
        <div className="vh-100 text-start d-flex align-items-center justify-content-start p-5">
          <div className="container">
            <div className="row">
              <div className="col-lg-6 col-md-12 justify-content-start">
                <div className="about-me-wrapper">
                  <div className="about-me pb-3">
                    <h2 className="about-title pb-1 blue-text">About Me.</h2>
                    <h4 className="about-text">
                      I am a forward thinking engineer who works effectively in
                      dynamic team environments as well as a self directed
                      setting. I am a quick learner that can pick up tech stacks
                      and adapt to the exponential growth of the tech industry.
                    </h4>
                  </div>
                  <div className="about-education pb-3">
                    <h2 className="about-title pb-1 blue-text">
                      I am dedicated to lifelong learning.
                    </h2>
                    <h4 className="about-text">
                      Bachelor of Science in Computer Science. Texas State
                      University, San Marcos. I have been coding for{" "}
                      {currentTime}
                    </h4>
                  </div>
                </div>
              </div>
              <div className="col-lg-5 col-md-12">
                <Smiley />
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
