// import React from "react";
import "./Projects.css";
import sortingvis from "../../assets/Project/sorting-visualizer.JPG";
import matchingdice from "../../assets/Project/matching-dice.JPG";
import checkerspy from "../../assets/Project/checker-pygame.JPG";
import pathfindimg from "../../assets/Project/pathfinding-visualizer.JPG";

export default function Projects() {
  return (
    <>
      <div id="project" className="project-wrapper">
        <div className="min-vh-100 px-5 d-flex align-items-xxl-center">
          <div className="container">
            <div className="project-padding">
              <div className="row">
                <div className="col-lg-4 col-md-6 col-12 py-3">
                  <div className="card bg-transparent">
                    <img
                      src={pathfindimg}
                      alt="null"
                      className="card-img-top"
                    ></img>
                    <div className="card-body">
                      <div className="ct-ct d-inline-block">
                        <h5 className="card-title">
                          Dijkstra's Algorithm Visualization Tool
                        </h5>
                        <p className="card-text">
                          This project utilizes Dijkstra's Algorithm and a min
                          heap data structure to find the shortest path between
                          two nodes.
                        </p>
                      </div>

                      <div className="card-button-container">
                        <a
                          href="https://masonwatson.github.io/pathfinding-visualizer/"
                          target="_blank"
                          rel="noreferrer"
                          class="btn btn-outline-light card-button"
                          onClick={(e) => e.currentTarget.blur()}
                        >
                          Visit the Application
                        </a>
                        <a
                          href="https://github.com/masonwatson/pathfinding-visualizer/tree/master"
                          target="_blank"
                          rel="noreferrer"
                          class="btn btn-outline-light card-button"
                          onClick={(e) => e.currentTarget.blur()}
                        >
                          View the Repository
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-lg-4 col-md-6 col-12 py-3">
                  <div className="card bg-transparent">
                    <img
                      src={sortingvis}
                      alt="null"
                      className="card-img-top"
                    ></img>
                    <div className="card-body">
                      <div className="ct-ct d-inline-block">
                        <h5 className="card-title">
                          Sorting Algorithm Visualization Tool
                        </h5>
                        <p className="card-text">
                          This project utilizes various sorting algorithms, such
                          as merge and bubble sort, to sort an array of random
                          data from smallest to largest.
                        </p>
                      </div>
                      <div className="card-button-container">
                        <a
                          href="https://masonwatson.github.io/sorting-visualizer/"
                          target="_blank"
                          rel="noreferrer"
                          class="btn btn-outline-light card-button"
                          onClick={(e) => e.currentTarget.blur()}
                        >
                          Visit the Application
                        </a>
                        <a
                          href="https://github.com/masonwatson/sorting-visualizer/tree/master"
                          target="_blank"
                          rel="noreferrer"
                          class="btn btn-outline-light card-button"
                          onClick={(e) => e.currentTarget.blur()}
                        >
                          View the Repository
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-lg-4 col-md-6 col-12 py-3">
                  <div className="card bg-transparent">
                    <img
                      src={checkerspy}
                      alt="null"
                      className="card-img-top"
                    ></img>
                    <div className="card-body">
                      <div className="ct-ct d-inline-block">
                        <h5 className="card-title">Checkers PyGame</h5>
                        <p className="card-text">
                          This project uses Python and the PyGame library to
                          implement the classic game of checkers.
                        </p>
                      </div>
                      <div className="card-button-container">
                        <a
                          href="https://github.com/masonwatson/checkers-pygame"
                          target="_blank"
                          rel="noreferrer"
                          class="btn btn-outline-light card-button"
                          onClick={(e) => e.currentTarget.blur()}
                        >
                          View the Repository
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-lg-4 col-md-6 col-12 py-3">
                  <div className="card bg-transparent">
                    <img
                      src={matchingdice}
                      alt="null"
                      className="card-img-top"
                    ></img>
                    <div className="card-body">
                      <div className="ct-ct d-inline-block">
                        <h5 className="card-title">Matching Dice Game</h5>
                        <p className="card-text">
                          An automated matching dice game written in C. This
                          project uses POSIX threads to determine player order.
                          The winning team is determined when both players on a
                          team roll the same total.
                        </p>
                      </div>
                      <div className="card-button-container">
                        <a
                          href="https://github.com/masonwatson/matching-dice-game"
                          target="_blank"
                          rel="noreferrer"
                          class="btn btn-outline-light card-button"
                          onClick={(e) => e.currentTarget.blur()}
                        >
                          View the Repository
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
