import "./Header.css";
import { Link } from "react-router-dom";
import useActiveLocationHook from "./headerHooks/useActiveLocationHook";

export default function Header() {
  // custom hooks
  const activeLocation = useActiveLocationHook();

  return (
    <>
      <nav
        id="navbar"
        className="navbar fixed-top navbar-expand-md bg-transparent navbar-dark d-flex justify-content-around w-100 pt-3 px-4"
      >
        <div className="container-fluid">
          <a className="navbar-brand" href="https://masonxwatson.com/">
            Mason Watson
          </a>
          <button
            class="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
            onClick={(e) => e.currentTarget.blur()}
          >
            <span class="navbar-toggler-icon"></span>
          </button>
          <div
            id="navbarSupportedContent"
            className="collapse navbar-collapse multi-collapse"
          >
            <ul className="navbar-nav me-auto mb-2 mb-md-0">
              <Link
                className={
                  "nav-link text-decoration-none" +
                  (activeLocation === "/" ? " active" : "")
                }
                to="/"
              >
                <li className="nav-item">Home</li>
              </Link>
              <Link
                className={
                  "nav-link text-decoration-none" +
                  (activeLocation === "/about" ? " active" : "")
                }
                to="/about"
              >
                <li className="nav-item">About</li>
              </Link>
              <Link
                className={
                  "nav-link text-decoration-none" +
                  (activeLocation === "/timeline" ? " active" : "")
                }
                to="/timeline"
              >
                <li className="nav-item">Timeline</li>
              </Link>
              <Link
                className={
                  "nav-link text-decoration-none" +
                  (activeLocation === "/projects" ? " active" : "")
                }
                to="/projects"
              >
                <li className="nav-item">Projects</li>
              </Link>
              <Link
                className={
                  "nav-link text-decoration-none" +
                  (activeLocation === "/contact" ? " active" : "")
                }
                to="/contact"
              >
                <li className="nav-item">Contact</li>
              </Link>
            </ul>
          </div>
        </div>
      </nav>
    </>
  );
}
