import "./Home.css";
import useHomeText from "./homeHooks/useHomeText";

const text_attributes = ["Software Engineer.", "Creative Problem Solver."];

export default function Home() {
  // Custom Hook
  const homeText = useHomeText(text_attributes);

  return (
    <>
      <div id="home" className="home-wrapper">
        <div className="vh-100 text-start d-flex align-items-center justify-content-start">
          <div className="container">
              <div className="home-intro pb-2">
                <span>
                  <span className="home-intro-space" />
                  HELLO WORLD
                </span>
              </div>
              <div className="home-text">
                <span>
                  I am Mason Watson, <br />
                  a {homeText}
                  <span className="blinking-cursor" />
                </span>
              </div>
          </div>
        </div>
      </div>
    </>
  );
}
